/* Reset b√°sico */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #f9f6f2; /* Cor de fundo suave */
    color: #4D2C1D; /* Cor marrom escuro para o texto */
    line-height: 1.6;
    margin-top: 70px; /* Define o espa√ßamento superior do conte√∫do */
}

/* Header */
.header {
    background-color: rgba(255, 248, 240, 0.7); /* Cor de fundo suave e transparente */
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.1); /* Mant√©m o sombreado */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    height: 70px; /* Altura fixa do cabe√ßalho */
    padding: 10px 0; /* Ajuste o padding do cabe√ßalho */
    backdrop-filter: blur(10px); /* Aplica um desfoque no fundo atr√°s do cabe√ßalho */
}

/* Conte√∫do do cabe√ßalho */
.header .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 15px;
}

.header .logo img {
    width: 85px; /* Reduzido o tamanho do logotipo */
}

/* Menu de navega√ß√£o */
.header nav ul {
    list-style: none;
    display: flex;
    gap: 20px; /* Aumentado o espa√ßo entre os itens */
    transition: transform 0.3s ease-in-out; /* Anima√ß√£o suave para o menu */
}

/* Links do menu */
.header nav ul li a {
    text-decoration: none;
    color: #4D2C1D; /* Cor de chocolate para os links */
    padding: 0.5em 0.4em; /* Reduzido o padding dos links */
    transition: background-color 0.3s, color 0.3s;
}

.header nav ul li a:hover {
    background-color: #6F4F1F; /* Cor de chocolate para o hover */
    color: white;
}

/* Mobile Menu */
.header .mobile-icon {
    cursor: pointer;
    display: none; /* Escondido por padr√£o */
    padding: 20px 15px; /* Reduzido o padding */
}

.header .mobile-icon .hamburguer {
    background: #4D2C1D; /* Cor de chocolate para a hamburguer */
    display: block;
    height: 2px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 20px; /* Aumentado o tamanho */
}

.header .mobile-icon .hamburguer:before,
.header .mobile-icon .hamburguer:after {
    background: #4D2C1D; /* Cor de chocolate para as partes da hamburguer */
    content: '';
    display: block;
    height: 2px; /* Tamanho ajustado */
    position: absolute;
    transition: all 0.2s ease-out;
    width: 100%;
}

.header .mobile-icon .hamburguer:before {
    top: -5px;
}

.header .mobile-icon .hamburguer:after {
    top: 5px;
}

/* Bot√£o de controle do menu (escondido por padr√£o) */
.header .mobile-btn {
    display: none; /* Escondido por padr√£o */
}

/* Menu aberto */
.header .mobile-btn:checked ~ nav ul {
    transform: translateX(0); /* Alinha o menu na tela */
    max-height: 240px; /* Ajuste conforme necess√°rio */
    display: flex; /* Mostra o menu */
    flex-direction: column; /* Alinha os itens verticalmente */
}

.header .mobile-btn:checked ~ .mobile-icon .hamburguer {
    background: transparent; /* Transforma a cor do hamb√∫rguer */
}

.header .mobile-btn:checked ~ .mobile-icon .hamburguer:before {
    transform: rotate(-45deg); /* Rotaciona para formar um "X" */
}

.header .mobile-btn:checked ~ .mobile-icon .hamburguer:after {
    transform: rotate(45deg); /* Rotaciona para formar um "X" */
}

/* Responsividade */
@media (max-width: 768px) {
    /* √çcone do menu */
    .header .mobile-icon {
        display: inline-block; /* Mostra o √≠cone do menu em dispositivos m√≥veis */
    }

    /* Itens do menu */
    .header nav ul {
        display: flex; /* Exibe o menu como flexbox */
        flex-direction: column; /* Organiza os itens em uma coluna */
        gap: 20px; /* Aumenta o espa√ßamento entre os itens */
        position: absolute;
        top: 70px; /* Abaixo do cabe√ßalho */
        left: 0;
        right: 0;
        background-color: #fff; /* Cor de fundo do menu */
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); /* Sombra suave */
        transform: translateX(-100%); /* Inicialmente escondido √† esquerda */
        padding: 20px;
    }

    /* Quando o bot√£o estiver marcado, o menu ser√° mostrado */
    .header .mobile-btn:checked ~ nav ul {
        transform: translateX(0); /* Move o menu para a tela */
    }

    /* Estilo do link no menu */
    .header nav ul li a {
        color: #4D2C1D; /* Cor dos links */
        padding: 15px; /* Ajustado o padding */
        text-align: center;
        font-size: 18px; /* Aumentado o tamanho da fonte */
    }

    /* Estilo do bot√£o de fechamento (X) */
    .header nav ul li a:hover {
        background-color: #6F4F1F;
        color: white;
    }
}

/* Ajuste do conte√∫do abaixo do cabe√ßalho */
main {
    padding-top: 80px; /* Define um espa√ßamento confort√°vel para o conte√∫do abaixo do cabe√ßalho */
}
